import { MouseEvent, RefObject, createContext } from "react";

/**
 * Context props for the Drawer component
 */
export type DrawerContextProps = {
  /**
   * Function to close the drawer.
   */
  close: (e?: MouseEvent<HTMLElement>) => void;
  /**
   * Reference to the drawer dialog element.
   */
  ref?: RefObject<HTMLDialogElement>;
};

export const DrawerContext = createContext<DrawerContextProps | null>(null);
